import { MapData } from '../constants'
import { Singleton } from './Singleton'
import { Cost } from './cost'

type Pollutant = string
type AQI = string


export abstract class AQIParam extends Cost {
    asCost(): { param: AQI | Pollutant, cost: Cost } {
        return {
            param: this.getId(),
            cost: this
        }
    }

    getMapData(): MapData {
        return MapData.AQ_MAP
    }
}

export abstract class AQISpecification {
    abstract asCosts(): Cost[]

    asCost(p: Pollutant | AQI): Cost | undefined {
        return this.asCosts().find(z => z.getId() === p)
    }
}