import axios from "axios"
import { Cost } from "../types/cost"
import { MapData } from "../constants"

export interface CostMapDataStatus {
    map_data_available: boolean
    map_data_datetime: number | null
}

const serverUrl = process.env.REACT_APP_ROUTES_SERVER || '/routes'
const aqServiceUrl = process.env.REACT_APP_AQ_SERVER || '/aq'

export const getCostsMapDataStatus = async (c: Cost): Promise<CostMapDataStatus> => {
    try {
        const response = await axios.get(serverUrl.concat(`map-data-status/${c.getMapData()}`))
        if (response.data) {
            return response.data as CostMapDataStatus
        }
    } catch (error) {
        console.error('could not fetch AQI map data status')
    }
    return { map_data_available: false, map_data_datetime: null }
}

export const getCostsLayerData = async (c: Cost): Promise<Map<number, any> | undefined> => {
    let response = await axios.get(serverUrl.concat(`map-data/${c.getMapData()}`))
    if (response.data) {
        const dataMap = new Map<number, number>()
        for (const e of response.data.data) {
            dataMap.set(e.id_ig, e)
        }
        return dataMap
    } else {
        console.error('no AQI map data available')
    }
}

export async function interpolatePositions(positions: number[][], c: Cost): Promise<any> {
    if (c.getMapData() === MapData.ROAD_ACCIDENTS_MAP) {
        const url = serverUrl.concat(`interpolate/${c.getMapData()}`)
        const requestData = {
            positions,
        }
        try {
            const response = await axios.post(url, requestData)
            if (response.status !== 200) {
                throw new Error(`Errore nella richiesta: ${response.status} ${response.statusText}`)
            }
            console.log(response.data)
            return response.data
        } catch (error) {
            console.error('Errore durante la richiesta:', error)
            throw error
        }
    } else {
        const url = `${aqServiceUrl}/interpolate`
        const requestData = {
            positions,
        }
        try {
            const response = await axios.post(url, requestData)
            if (response.status !== 200) {
                throw new Error(`Errore nella richiesta: ${response.status} ${response.statusText}`)
            }

            return response.data
        } catch (error) {
            console.error('Errore durante la richiesta:', error)
            throw error
        }
    }
}

