import axios from 'axios'

const API_BASE_URL = process.env.REACT_APP_USERS_SERVER || '/users/'

export const updateHomePosition = async (homePosition: { lat: number; lng: number }) => {
  try {
    const response = await axios.post(`${API_BASE_URL}update_home`, { homePosition }, {
      headers: {
        'Authorization': `Bearer ${localStorage.getItem('ecosister-token')}`,
        'Content-Type': 'application/json',
      },
    })

    if (response.status === 200) {
      return response.data
    } else {
      throw new Error('Update home position failed')
    }
  } catch (error) {
    console.error('Update home position error:', error)
    return null
  }
}

