import { AQIParam, AQISpecification } from "./AQISpecification";
import { Category, Cost, RangeMapping, UnitOfMeasure } from "./cost";

// Sostituisci '#HexColor' con il colore esatto
const euroAQICategories: Category[] = [
    { description: 'Good', color: '#50f0e6' },
    { description: 'Fair', color: '#50ccaa' },
    { description: 'Moderate', color: '#f0e641' },
    { description: 'Poor', color: '#ff5050' },
    { description: 'Very Poor', color: '#960032' },
    { description: 'Extremely Poor', color: '#7d2181' },
]

export class EuroPM25 extends AQIParam {
    getNameInData(): string {
        return 'PM25'
    }
    getCategories(): Category[] {
        return euroAQICategories
    }
    getRangeMapping(): RangeMapping {
        return [0, 10, 10, 20, 20, 25, 25, 50, 50, 75, 75, 800]
    }
    getUnitOfMeasure(): UnitOfMeasure {
        return 'µg/m³'
    }
}

export class EuroPM10 extends AQIParam {
    getNameInData(): string {
        return 'PM10'
    }
    getCategories(): Category[] {
        return euroAQICategories
    }
    getRangeMapping(): RangeMapping {
        return [0, 20, 20, 40, 40, 50, 50, 100, 100, 150, 150, 1200]
    }
    getUnitOfMeasure(): UnitOfMeasure {
        return 'µg/m³'
    }
}

