import { Action } from 'redux'
import { AuthReducer, User } from '../types/types'


const initialAuthState: AuthReducer = {
  user: null,
  showLoginModal: false,
  showRegisterModal: false,
}

interface AuthAction extends Action {
  user: User
}

const authReducer = (store: AuthReducer = initialAuthState, action: AuthAction): AuthReducer => {
  switch (action.type) {
    case 'LOGIN_SUCCESS':
      return { ...store, user: action.user }
    case 'LOGOUT_SUCCESS':
      return { user: null, showLoginModal: false, showRegisterModal: false }
    case 'OPEN_LOGIN_MODAL':
      return { ...store, showLoginModal: true }
    case 'CLOSE_LOGIN_MODAL':
      return { ...store, showLoginModal: false }
    case 'OPEN_REGISTER_MODAL':
      return { ...store, showRegisterModal: true }
    case 'CLOSE_REGISTER_MODAL':
      return { ...store, showRegisterModal: false }
    default:
      return store
  }
}


export const loginSuccess = (user: User) => {
    return { type: 'LOGIN_SUCCESS', user }
}

export const logoutSuccess = () => {
    return { type: 'LOGOUT_SUCCESS' }
}

export const showLoginModal = () => {
  return { type: 'OPEN_LOGIN_MODAL' }
}

export const closeLoginModal = () => {
  return { type: 'CLOSE_LOGIN_MODAL' }
}

export const showRegisterModal = () => {
  return { type: 'OPEN_REGISTER_MODAL' }
}

export const closeRegisterModal = () => {
  return { type: 'CLOSE_REGISTER_MODAL' }
}
export default authReducer
