import React from 'react'
import styled from 'styled-components'
import { connect, ConnectedProps } from 'react-redux'
import { Basemap, MapId } from '../../constants'
import LoadAnimation from './../LoadAnimation/LoadAnimation'
import T from '../../utils/translator/Translator'
import { exposureCostMappingDefault, ReduxState } from '../../types/types'
import { Category } from '../../types/cost'


const Container = styled.div`
  margin: 7px 5px 5px 5px;
`
const LegendBox = styled.div`
  background-color: #000000c2;
  color: white;
  padding: 8px 8px 8px 8px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: max-content;
`
const TitleRow = styled.div`
  display: flex;
  justify-content: center;
  margin: 1px 5px 9px 5px;
`
const ColorRow = styled.div`
  display: flex;
  margin-bottom: 3px;
`
const ColorBox = styled.div`
  border-radius: 4px;
  margin: 0px 3px;
  width: 15px;
  height: 15px;
`
const LabelRow = styled.div`
  display: flex;
  justify-content: space-between;
  margin: 2px 0px 1px 0px;
  width: calc(100% - 6px);
`
const LoadAnimationWrapper = styled.div`
  margin: 7px 6px 7px 5px;
`

const ZoomTooltipBox = styled.div`
  padding: 9px 11px;
  color: white;
  font-weight: 400;
  text-align: center;
  max-width: 160px;
  letter-spacing: 1px;
  background-color: #0000004f;
  border-radius: 5px;
  margin: 4px 0 0 0;
  line-height: 120%;
`

const formatUtcSecondsToAmPm = (utcSeconds: number): string => {
  const date = new Date(0)
  date.setUTCSeconds(utcSeconds)
  let hours = date.getHours()
  const ampm = hours >= 12 ? 'pm' : 'am'
  hours = hours % 12
  hours = hours ? hours : 12
  return hours + ':30 ' + ampm
}

const getDataAgeMinutes = (dataTimeUtcSecs: number): number => {
  const seconds = Date.now() / 1000
  return Math.round((seconds - dataTimeUtcSecs) / 60)
}

const formatAqTimeInfo = (dataTimeUtcSecs: number): string => {
  const dataAgeMinutes = getDataAgeMinutes(dataTimeUtcSecs)
  if (dataAgeMinutes > 60) {
    return formatUtcSecondsToAmPm(dataTimeUtcSecs)
  } else {
    return 'basemap.air_quality.legend.title.aq_data_time_now'
  }
}

const MapLegend = (props: PropsFromRedux) => {
  const { loadingData, waitingStyleUpdate, updatingStyle } = props.aqLayer

  const costToUse = props.mapId === MapId.HEATMAP || props.mapId === MapId.STREET_BY_STREET ? props.cost :
    props.showingPathsOfExposureMode !== null ? exposureCostMappingDefault[props.showingPathsOfExposureMode] : props.cost
  return (
    <Container>
      <LegendBox>
        <TitleRow>
          {costToUse.getId()}
        </TitleRow>
        <ColorRow>
          {costToUse.getCategories().map((k: Category) => (
            <ColorBox key={k.description} style={{ backgroundColor: k.color }} />
          ))}
        </ColorRow>
        <LabelRow>
          <div>
            <T>basemap.air_quality.legend.label.good</T>
          </div>
          <div>
            <T>basemap.air_quality.legend.label.poor</T>
          </div>
        </LabelRow>
      </LegendBox>
      {props.mapZoom < 12 && props.basemap === Basemap.STREETS_NIGHT && (
        <ZoomTooltipBox>
          <T>basemap.air_quality.zoom_closer_tip</T>
        </ZoomTooltipBox>
      )}
      {(loadingData || waitingStyleUpdate || updatingStyle) && props.mapZoom >= 12 && (
        <LoadAnimationWrapper>
          <LoadAnimation size={30} color={'white'} />
        </LoadAnimationWrapper>
      )}
    </Container>
  )
}

const mapStateToProps = (state: ReduxState) => ({
  mapId: state.map.mapId,
  aqLayer: state.airQualityLayer,
  mapZoom: state.map.zoom,
  basemap: state.map.basemap,
  cost: state.cost.cost,
  showingPathsOfExposureMode: state.paths.showingPathsOfExposureMode,
})

const connector = connect(mapStateToProps, {})
type PropsFromRedux = ConnectedProps<typeof connector>
export default connector(MapLegend)
