import { Component } from 'react'
import { connect, ConnectedProps } from 'react-redux'
import {
  setLoadingData,
  handleDataNotAvailable,
  setDataLoaded,
  setDelayedStyleUpdate,
  setWaitingStyleUpdate,
  setUpdatingStyle,
} from './../../reducers/airQualityLayerReducer'
import { setLayerLoaded } from './../../reducers/mapReducer'
import { MapId, LayerId, MapData } from '../../constants'
import * as aq from '../../services/aq'
import { MbMap, ReduxState } from '../../types/types'
import Area from '../../Borders.json'
import * as turf from '@turf/turf'
import { Cost } from '../../types/cost'
import { interpolatePositions } from '../../services/cost'

function getColorFromValue(value: number, cost: Cost) {

  let c = cost.getCategoryFromValue(value)
  if (c) {
    return c.color
  } else {
    return '#964b00'
  }
}

class AirQualityHeatmap extends Component<PropsFromRedux & { map?: MbMap }> {
  layerId = LayerId.HEATMAP
  source = 'turfgrid'
  data: any | undefined = undefined
  dataTimeUtcSecs: number | null = null
  mapData: MapData | null = null

  hideLayer(map: any) {
    map.setLayoutProperty(this.layerId, 'visibility', 'none')
  }

  createGrid = (polygon: any, gridSize: any) => {
    const grid = turf.featureCollection([])

    // Determina i limiti del poligono
    const bbox = turf.bbox(polygon)
    const [minX, minY, maxX, maxY] = bbox

    // Loop per generare la griglia
    for (let x = minX; x < maxX; x += gridSize) {
      for (let y = minY; y < maxY; y += gridSize) {
        // Crea un quadrato
        const square = turf.polygon([
          [
            [x, y],
            [x + gridSize, y],
            [x + gridSize, y + gridSize],
            [x, y + gridSize],
            [x, y], // Chiude il poligono ripetendo il primo punto
          ],
        ])

        // Verifica se il centro del quadrato è all'interno del poligono
        const center = turf.point([x + gridSize / 2, y + gridSize / 2])
        if (turf.booleanPointInPolygon(center, polygon)) {
          grid.features.push(square)
        }
      }
    }

    return grid
  }

  gridPolygons = this.createGrid(Area.features[0].geometry, 0.001)

  assignGradientColors(grid: any, values: any, cost: Cost) {
    grid.features.forEach((polygon: any) => {
      const centroid = turf.centroid(polygon).geometry.coordinates
      const matchingValue = values.find(
        (v: any) => v.position[0] === centroid[0] && v.position[1] === centroid[1],
      )
      if (matchingValue[cost.getNameInData()]) { }
      const color = matchingValue ? getColorFromValue(matchingValue[cost.getNameInData()], cost) : '#00E400' // Usa un colore di default se non c'è corrispondenza
      polygon.properties = { color }
    })
  }

  updateCostState = (map: any, gridPolygons: any, cost: Cost) => {
    this.props.setUpdatingStyle(true)
    this.props.setWaitingStyleUpdate(false)

    this.assignGradientColors(this.gridPolygons, this.data, cost)

    if (!map.getLayer(this.layerId)) {
      map.addSource(this.source, {
        type: 'geojson',
        data: gridPolygons,
      })

      map.addLayer({
        id: this.layerId,
        type: 'fill',
        source: this.source,
        paint: {
          'fill-color': ['get', 'color'],
          'fill-opacity': 0.5, // Set opacity to 0.5
          'fill-outline-color': 'rgba(0, 0, 0, 0)', // Transparent outline
        },
      })
    } else {
      map.getSource(this.source).setData(gridPolygons)
    }
    this.props.setDelayedStyleUpdate(400)
    this.props.setLayerLoaded(this.layerId)
    this.props.setUpdatingStyle(false)
  }

  /**
   * Triggers AQI map data fetch/update if new or initial AQI data is available.
   */
  maybeUpdateAqData = async (repeat: boolean = true) => {
    if (
      this.props.mapId === MapId.HEATMAP && !this.props.layer.loadingData) {
      this.props.setLoadingData(true)
      try {
        this.data = await interpolatePositions(
          this.gridPolygons.features.map(p => turf.centroid(p)).map(f => f.geometry.coordinates),
          this.props.cost
        )
        this.mapData = this.props.cost.getMapData()
        this.data = this.data.positions
        this.props.setDelayedStyleUpdate(100)
      } catch (error) {
        console.log(error)
      }
      this.props.setLoadingData(false)
    }
    if (repeat) {
      setTimeout(this.maybeUpdateAqData, 40000)
    }
  }

  componentDidMount = async () => {
    this.maybeUpdateAqData(true)
    this.props.map!.on('sourcedata', e => {
      if (e.isSourceLoaded &&
        this.props.mapId === MapId.HEATMAP &&
        this.data) {
        this.props.setDelayedStyleUpdate(500)
      }
    })
  }

  /**
   * Let's update AQI features' states only after a short delay after either changed map view or new
   * source data download. This way we can avoid unnecessarily updating the feature states multiple
   * times due to single change in map view.
   */
  componentDidUpdate = async (prevProps: PropsFromRedux) => {
    // @ts-ignore - map is given to all children of Map
    const { map } = this.props

    //hidden the layer if the map is not selected
    if (this.props.mapId !== MapId.HEATMAP) {
      if (map?.getLayer(this.layerId) !== undefined)
        map.setLayoutProperty(this.layerId, 'visibility', 'none')
      return
    }

    //if layer exist and map is selected set visible true
    if (map?.getLayer(this.layerId) !== undefined)
      map.setLayoutProperty(this.layerId, 'visibility', 'visible')

    // add delayed AQI map update to "queue" if basemap changes
    if (prevProps.mapId !== MapId.HEATMAP || prevProps.cost.getId() !== this.props.cost.getId()) {
      if (!this.data || this.mapData !== this.props.cost.getMapData()) {
        await this.maybeUpdateAqData(false)
      } else {
        this.props.setDelayedStyleUpdate(200)
      }
      if (!this.data) {
        this.props.handleDataNotAvailable()
      }
    }

    // add delayed AQI map update to "queue" if map center changes
    if (JSON.stringify(prevProps.mapCenter) !== JSON.stringify(this.props.mapCenter) && this.data) {
      this.props.setDelayedStyleUpdate(1300)
    }

    // implement the AQI style update if and only after all update delays have passed
    if (
      this.props.layer.waitingStyleUpdate &&
      this.props.layer.styleUpdateDelays === 0 &&
      prevProps.layer.styleUpdateDelays > 0 &&
      this.props.map &&
      this.data &&
      this.mapData === this.props.cost.getMapData()
    ) {
      // trigger AQI map style update if necessary
      this.updateCostState(this.props.map, this.gridPolygons, this.props.cost)
    }
  }

  render() {
    return null
  }
}

const mapStateToProps = (state: ReduxState) => ({
  mapId: state.map.mapId,
  layer: state.airQualityLayer,
  mapCenter: state.map.center,
  cost: state.cost.cost,
})

const connector = connect(mapStateToProps, {
  setLoadingData,
  handleDataNotAvailable,
  setDataLoaded,
  setWaitingStyleUpdate,
  setUpdatingStyle,
  setDelayedStyleUpdate,
  setLayerLoaded,
})
type PropsFromRedux = ConnectedProps<typeof connector>
export default connector(AirQualityHeatmap)
