import { MapData } from "../constants"
import { Singleton } from "./Singleton"

export type Category = {
    description: string
    color: string
}


// Array di numeri che rappresentano intervalli consecutivi [minValue1, maxValue1, minValue2, maxValue2, ...]
export type RangeMapping = number[]

export type UnitOfMeasure = 'µg/m³' | 'ppm' | 'mg/m³' | 'ppb' | 'dimensionless'


export abstract class Cost {
    abstract getCategories(): Category[]
    abstract getRangeMapping(): RangeMapping
    abstract getUnitOfMeasure(): UnitOfMeasure
    abstract getMapData(): MapData

    getId(): string {
        return this.constructor.name
    }

    getNameInData(): string {
        return this.getId()
    }


    getCategoryInfo(id: number): Category {
        return this.getCategories()[id - 1]
    }

    getCategoryFromValue(value: number): Category | undefined {
        const category = this.getCategories()[this.getCategoryIndex(value)]
        return category
    }

    getCategoryColorMapping(): [number, string][] {
        return this.getCategories().map((c, i) => [i, c.color])
    }

    getCategoryIndex(value: number): number {
        const ranges = this.getRangeMapping()

        for (let i = 0; i <= ranges.length - 2; i += 2) {
            const minValue = ranges[i]
            const maxValue = ranges[i + 1]

            if (value >= minValue && value <= maxValue) {
                return i / 2
            }
        }

        throw new Error(`Value ${value} does not match any range this cost`)
    }

    getCategoryPercent(category: number): number {
        return category / this.getCategories().length
    }

}
