import { Action } from 'redux'
import { CostReducer } from '../types/types'
import { EuroAQI } from '../types/EuroAqiSpec'
import { Cost } from '../types/cost'

const initialCostState: CostReducer = {
  cost: new EuroAQI(),
  prev: new EuroAQI(),
}

interface CostAction extends Action {
  cost: Cost
}

const costReducer = (state = initialCostState, action: CostAction) => {
  switch (action.type) {
    case 'SET_COST':
      console.log(`dispatched option with ${action.cost}`)
      let prev = state.cost
      return { prev: prev, cost: action.cost }
    default:
      return state
  }
}

export const setCost = (cost: Cost): CostAction => ({
  type: 'SET_COST',
  cost: cost,
})

export default costReducer
